.App {
  text-align: center;
  display: block;

}

.Pixel {
  width: 10px;
  height: 10px;
  border: 1.5px solid white;
  display: inline-block;
  margin: 0px;
}

.apple {
  background-color: #F20505;
  border-radius: 50%;
}

.rock {
  background-color: gray;
  border-radius: 50%;
  
}

.snake {
  background-color: #15ff00;
}

.Board {
  width: 130px;
  height: 130px;
  
  display: block;
  margin: 13vh auto 5vh;
  line-height: 0%;

}

.white {
  background: white;
}

.keyboard {
  /* border: 1px solid black; */
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  
  margin: 0 auto; 
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-bottom: 10% ;
  
}

.keyboard-inner-box {
  /* border: 1px solid black; */
  width: 135px;
  display: flex;
  justify-content: center;
  
}

.keyboard-inner-box:nth-of-type(2) {
  justify-content: space-between;
}

.btn-arrow {
  padding: 0 20px;
  font-family: 'VT323', monospace;
  font-size: 1.5rem;
  color: 	#A9A9A9;
  width: 35px;
  height: 44px;
  border-radius: 0%;
  text-indent: -8px;

}

.start-button {
  color: #03FE03;
  font-size: 2em;
  font-family: 'VT323', monospace;
  text-decoration: underline;
  background-color: rgba(0,0,0,0);
}

.lost-window {
  
    display: block;
    width: 150px;
    height: 150px;
    position: fixed;

    background-color: #33414E;
    text-align: center;
    bottom: calc(50% - 75px);
    left: calc(50% - 75px);   
    box-shadow: 9px 10px 16px -12px rgba(0,0,0,0.75);
}